<template>
    <div>
        <ui-pdf-panel :header="''" :class="'ma-0'">
            <div class="td b_son">
                <div class="w-17">SKU</div>
                <div class="w-21"></div>
                <div class="w-21"></div>
                <div class="w-7"></div>
                <div class="w-8"></div>
                <div class="w-8"></div>
                <div class="w-18"></div>
            </div>
            <div class="pt_s"></div>
            <div class="td td-data fx-l"
                v-for="(v, i) in _items" :key="i"
            >
                <div class="w-17 pr_s t-wrap">{{ v.product.sku }}</div>
                <div class="w-21 pr_s">{{ named(v) }}</div>
                <div class="w-21 pr_s">{{ view.ser_timed(v.date, false , false) }}</div>
                <div class="w-7">
                    <inventory-opera-viewing></inventory-opera-viewing>
                </div>
                <div class="w-8">
                    <span class="tag-inven">
                        <company-named-viewing :named="v.stock_location" v-if="v.stock_location"></company-named-viewing>
                    </span>
                </div>
                <div class="w-8">{{ v.difference }}</div>
                <div class="w-18">
                    {{ v.mark }}
                    <!--
                    <span class="fx-l"><span class="tag-inven"></span>&nbsp;{{ v.invens.all }}</span>
                    <span class="fx-l"><span class="tag-inven"></span>&nbsp;{{ v.invens.fire }}</span>
                    <span class="fx-l"><span class="tag-inven"></span>&nbsp;{{ v.invens.salt }}</span>-->
                </div>
            </div>
        </ui-pdf-panel>
    </div>
</template>

<script>
import CompanyNamedViewing from '../../../../components/Viewing/Company/CompanyNamedViewing.vue'
import InventoryOperaViewing from '../../../../components/Viewing/Record/Inventory/InventoryOperaViewing.vue'
import UiPdfPanel from '../../layout/UiPdfPanel.vue'
    export default {
        components: { UiPdfPanel, CompanyNamedViewing, InventoryOperaViewing },
        name: '',
        props: [ '_items' ],
        mounted() {
            console.log(' =', this._items)
        },
        data() {
            return {
                
                datas: [
                    { sku: 'SY10000222', named_en: 'BASIL GOOD PRODUCT', named_ch: '', price: '333.3', pv: 120, date: '2012-12-12 23:23', invens: '', num: 50, typed: '', mark: '' },  
                    { sku: 'SY10000223', named_en: 'BASIL GOOD PRODUCT 2', named_ch: ' 2', price: '333.3', pv: 120, date: '2012-12-13 23:23', invens: '', num: 1, typed: '', mark: '' },  
                    { sku: 'SY10000224', named_en: 'BASIL GOOD PRODUCT 3', named_ch: ' 3', price: '333.3', pv: 120, date: '2012-12-14 23:23', invens: '', num: 1, typed: '', mark: '' },  
                    { sku: 'SY10000225', named_en: 'BASIL GOOD PRODUCT 4', named_ch: ' 4', price: '333.3', pv: 120, date: '2012-12-15 23:23', invens: '', num: 999, typed: '', mark: '' },  
                ]
            }
        },
        methods: {
            named(v) {
                let res = v.product.translation[1].product_name
                v.product.translation.map(e => {
                    if (e.language_key == 'hk') {
                        res = e.product_name
                    }
                 })
                return res
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


