<template>
    <span>{{ txt }}</span>
</template>

<script>
    export default {
        name: '',
        props: [
            'opr'
        ],
        computed: {
            txt() {
                let res = ''
                if (this.opr == 1) {
                    res = ''
                }
                return res
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


